<template>
    <div class="modal-content">
        <div v-if="action==='edit'" class="title">{{$localizeFilter('change_financial_password_title')}}</div>
        <div v-if="action==='add'" class="title">{{$localizeFilter('add_financial_password_title')}}</div>
        <div class="window-text">{{ $localizeFilter('financial_confirmation_code_input')}}</div>
        <div class="input-field">
            <input
            id="code"
            type="text"
            v-model.trim="code"
            :class="{ invalid: v$.code.$error }"
            />
            <small
            class="error"
            v-for="(error, index) of v$.code.$errors"
            :key="index"
            >
            {{ error.$message }}
            </small>
      </div>
      <div class="button-flex">
        <button @click="changeFinancialPassword()" class="green-button">{{$localizeFilter('confirm')}}</button>
      </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import useVuelidate from "@vuelidate/core";
import { required, helpers, minLength, numeric } from "@vuelidate/validators";

export default defineComponent({
    components: {
    },
    props: {
      action:{
        type: String,
        required: true
      },
      password:{
        type: String,
        required:true
      }
    },
    data: () => ({
      code: null
    }),
    setup() {
      return { v$: useVuelidate() };
    },
    validations() {
      return {
        code: {
          required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
          numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric)
        },
      };
    },
    methods: {
        closeModal() {
            this.$emit('closeModal')
        },
        async changeFinancialPassword() {
          this.v$.$touch();
          if (this.v$.$error) { return }
          const data = {password: this.password, code: this.code}
          await this.$store.dispatch('changeFinancialPassword', data)
          if (this.action === 'add') {
            this.$message(localizeFilter('financial_password_added'))
          } else {
            this.$message(localizeFilter('financial_password_changed'))
          }
          this.closeModal()
        },
    }
})
</script>

