<template>
    <div class="modal-content">
        <div v-if="action==='edit'" class="title">{{$localizeFilter('change_financial_password_title')}}</div>
        <div v-if="action==='add'" class="title">{{$localizeFilter('add_financial_password_title')}}</div>
        <div class="window-text" v-if="action==='edit'">{{ $localizeFilter('change_financial_password_first_input')}}</div>
        <div class="window-text" v-if="action==='add'">{{ $localizeFilter('add_financial_password_first_input')}}</div>
        <div class="input-field">
            <input
            id="first"
            type="text"
            v-model.trim="first"
            :class="{ invalid: v$.first.$error }"
            />
            <small
            class="error"
            v-for="(error, index) of v$.first.$errors"
            :key="index"
            >
          
          {{ error.$message }}
        </small>
        </div>
        <div class="window-text" v-if="action==='edit'">{{ $localizeFilter('change_financial_password_second_input')}}</div>
        <div class="window-text" v-if="action==='add'">{{ $localizeFilter('add_financial_password_second_input')}}</div>
        <div class="input-field">
            <input
            id="second"
            type="text"
            v-model.trim="second"
            :class="{ invalid: v$.second.$error || passwordsNotMatch  }"
            />
            <small
            class="error"
            v-for="(error, index) of v$.second.$errors"
            :key="index"
            >
            {{ error.$message }}
            </small>

            <small
            class="error"
            v-if="passwordsNotMatch"
            >
            {{ $localizeFilter('passwords_not_match') }}
            </small>
      </div>
      <div class="button-flex">
        <button @click="requestChangeFinancialPassword()" class="green-button">{{$localizeFilter('continue')}}</button>
      </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import useVuelidate from "@vuelidate/core";
import { required, helpers, minLength } from "@vuelidate/validators";

export default defineComponent({
    components: {
    },
    props: {
      action:{
        type: String,
        required: true
      }
    },
    data: () => ({
      first: null,
      second: null,
      passwordsNotMatch: false
    }),
    setup() {
      return { v$: useVuelidate() };
    },
    validations() {
      return {
        first: {
          required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
          minLength: helpers.withMessage(localizeFilter('min_length', [8]), minLength(8)),
        },
        second: {
          required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
          minLength: helpers.withMessage(localizeFilter('min_length', [8]), minLength(8)),
        }
      };
    },
    methods: {
        closeModal() {
            this.$emit('closeModal')
        },
        async requestChangeFinancialPassword() {
          this.passwordsNotMatch = false
          this.v$.$touch();
          if (this.v$.$error) { return }
          if (this.first !== this.second) {
            this.passwordsNotMatch = true
            return
          }
          await this.$store.dispatch('requestChangeFinancialPassword')
          this.$emit('requestChangeFinancialPasswordSuccessful', this.first)
        },
    }
})
</script>

